.logo {
  height: 64px;
  position: relative;
  line-height: 64px;
  transition: all 0.3s;
  background: #002140;
  overflow: hidden;
  text-align: center;
}
.logo h1 {
  color: white;
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
  margin: 0 0 0 12px;
  font-family: 'Myriad Pro', 'Helvetica Neue', Arial, Helvetica, sans-serif;
  font-weight: 600;
}
.nav-link {
  display: inline-block !important;
  color: rgba(255, 255, 255, 0.65);
  text-decoration: none;
  width: 100%;
}
.nav-link:hover {
  color: rgba(255, 255, 255);
}
:global .logo-img {
  display: inline-block;
  vertical-align: middle;
  color: #fff;
  font-size: 36px;
}
